
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import Info from './List'
var fly = require("flyio")
export default {
name: "Home",
components: {
ViewBox,
Info,
XHeader
},
data () {
return {
header: '订单列表',
isShow: false,
info: []
}
},
methods: {
getData(data) {
this.info = data
}
},
mounted () {
this.$nextTick(function () {
fly.get('https://api.xiangxing12.com/infoserver').then((res) => {
this.getData(res.data)
this.isShow = true
}).catch((error) => {
this.isShow = false
});
})
}
}
